import Modal from 'react-bootstrap/Modal';

function ItemModal({show, modalItem, handleClose, headers}) {
  const {title, description, imdb, channel, imdbId} = modalItem
  const {startYear, runtimeMinutes, genres, score, votes, scoreAlt} = imdb
  const { location} = channel

  const imdbScoreHeader = headers.find( h => h.name === 'rating')
  const altScoreHeader = headers.find( h => h.name === 'rating+')
  const votesHeader = headers.find( h => h.name === 'votes')
  const channelHeader = headers.find( h => h.name === 'channel')

  const getYear = num => (num > 0) ? `(${num})`: ''
  const getGenres = genres => (genres.length === 0) ? 'Unavailable' : genres.join(', ')
  const getDuration = duration => (duration < 0) ? 'Unknown' : `${duration} minutes`
  const getChannel = () => channelHeader.showVal(modalItem)
  const getScore = num => (num < 0) ? 'Unavailable' : imdbScoreHeader.showVal(modalItem)
  const getScoreAlt = num => (num < 0) ? 'Unavailable' : altScoreHeader.showVal(modalItem)
  const getVotes = num => (num < 0) ? 'Unavailable' : votesHeader.showVal(modalItem)
  const imdbHref = `https://www.imdb.com/title/${imdbId}`
  const imdbButton = imdbId ? <button className='btn btn-dark'><a className='linkButton' target="_blank" rel="noreferrer" href={imdbHref}>Visit IMDb Page</a></button> : ''

  return (
    <Modal show={show} onHide={handleClose}>
      <Modal.Header closeButton>
        <Modal.Title>{title} {getYear(startYear)}</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <p>{description}</p>
        <dl className="row">
          <dt className="col-6">Duration:</dt>
          <dd className="col-6">{getDuration(runtimeMinutes)}</dd>
          <dt className="col-6">Genres:</dt>
          <dd className="col-6">{getGenres(genres)}</dd>
          <dt className="col-6">Broadcaster:</dt>
          <dd className="col-6">{getChannel()}</dd>
        </dl>

        <dl className="row">
          <dt className="col-6">IMDb Rating:</dt>
          <dd className="col-6">{getScore(score)}</dd>
          <dt className="col-6">IMDb Votes:</dt>
          <dd className="col-6">{getVotes(votes)}</dd>
          <dt className="col-6">IMDb Alt Score:</dt>
          <dd className="col-6">{getScoreAlt(scoreAlt)}</dd>
        </dl>

        <div className='d-flex justify-content-around'>
          <button className='btn btn-dark'>
            <a className='linkButton' target="_blank" rel="noreferrer" href={location}>Watch Film</a>
          </button>
          {imdbButton}
        </div>
      </Modal.Body>
      <Modal.Footer>
      </Modal.Footer>
    </Modal>
  )
}

export default ItemModal