import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import SingleSelect from '../misc/SingleSelect';
import MultiSelect from '../misc/MultiSelect'

function Filters({ showSettings, handleClose, userFilterPicks, setUserFilterPicks, handleClickSweep, filmsInfo }) {
  const {filmGenres, filmRatings, filmRatingsPlus, filmDurations, filmTypes, filmDecades, filmVotes} = filmsInfo.options
  return (
    <>
      <Modal
        show={showSettings}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title>Filters</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <MultiSelect label='Decades:' propName='decades' current={userFilterPicks.decades} setCurrent={setUserFilterPicks} choices={filmDecades}/>
          <hr/>
          <MultiSelect label='Genres:' propName='genres' current={userFilterPicks.genres} setCurrent={setUserFilterPicks} choices={filmGenres}/>
          <hr/>
          <SingleSelect label='Votes:' propName='votes' current={userFilterPicks.votes} setCurrent={setUserFilterPicks} choices={filmVotes}/>
          <hr/>
          <SingleSelect label='Rating:' propName='ratings' current={userFilterPicks.ratings} setCurrent={setUserFilterPicks} choices={filmRatings}/>
          <hr/>
          <SingleSelect label='Rating+:' propName='ratingsPlus' current={userFilterPicks.ratingsPlus} setCurrent={setUserFilterPicks} choices={filmRatingsPlus}/>
          <hr/>
          <SingleSelect label='Duration:' propName='durations' current={userFilterPicks.durations} setCurrent={setUserFilterPicks} choices={filmDurations}/>
          <hr/>
          <MultiSelect label='Type:' propName='types' current={userFilterPicks.types} setCurrent={setUserFilterPicks} choices={filmTypes}/>
          
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={handleClickSweep}><i className="fa-solid fa-broom"></i></Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default Filters