import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';

export default function NavBar({ view, setView}) {
  const handleClick = e => {
    const name = e.target.id.split('-')[0]
    console.log(name)
    setView(name)
  }

  return (
    <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
      <Container>
        <Navbar.Brand href="#home">
          <img
            src="https://upload.wikimedia.org/wikipedia/commons/thumb/8/8c/United_Kingdom_film_clapperboard.svg/282px-United_Kingdom_film_clapperboard.svg.png"
            width="30"
            height="30"
            className="d-inline-block align-top"
            alt="Free Films UK logo"
          />{' '}
          Free To View Films UK
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav" className="justify-content-end">
          <Nav>
            <Nav.Link id="films-link" href="#films" onClick={handleClick}>Films</Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}