import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import Settings from './Settings';
import Filters from './Filters'
import Order from './Order'
import Search from './Search';
import getUserFilterPicksDefault from '../../funcs/getUserFilterPicksDefault'

function FilmsHeader({ headers, userSettingPicks, setUserSettingPicks, userFilterPicks, setUserFilterPicks, filmsInfo}) {
  const [showFilters, setShowFilters] = useState(false)
  const handleCloseFilters = () => setShowFilters(false);
  const handleShowFilters = () => setShowFilters(true);

  const [showSettings, setShowSettings] = useState(false)
  const handleCloseSettings = () => setShowSettings(false);
  const handleShowSettings = () => setShowSettings(true);

  const handleClickSweep = () => setUserFilterPicks(getUserFilterPicksDefault())
  
  return (
    <Navbar expand="lg" variant="light" bg="light">
      <Container>
        <div className='d-flex'>
          <Button variant="outline-secondary" className='m-1' size="sm" onClick={handleShowSettings}><i className="fa-solid fa-sliders"></i></Button>
          <Order headers={headers} userSettingPicks={userSettingPicks} setUserSettingPicks={setUserSettingPicks}/>
          <Settings showSettings={showSettings} handleClose={handleCloseSettings} userSettingPicks={userSettingPicks} setUserSettingPicks={setUserSettingPicks}/>
        </div>
        <div className='d-flex'>
          <Search userFilterPicks={userFilterPicks} setUserFilterPicks={setUserFilterPicks}/>
          <Button variant="outline-secondary" className='m-1' size="sm" onClick={handleShowFilters}><i className="fa-solid fa-filter"></i></Button>
          <Filters showSettings={showFilters} handleClose={handleCloseFilters} userFilterPicks={userFilterPicks} setUserFilterPicks={setUserFilterPicks} handleClickSweep={handleClickSweep} filmsInfo={filmsInfo}/>
          <Button variant="outline-secondary" className='m-1' size="sm" onClick={handleClickSweep}><i className="fa-solid fa-broom"></i></Button>
        </div>
      </Container>
    </Navbar>
  )
}

export default FilmsHeader