export default function filterFilms( films, options) {
  const { search, votes, decades, genres, ratings, ratingsPlus, durations, types } = options

  let filteredFilms = films
  
  // search term
  if(search.length > 0) {
    filteredFilms = filterSearchTerm(filteredFilms, search)
  }

  // votes
  if(votes) {
    const {value} = votes
    filteredFilms = filteredFilms.filter(film => film.imdb.votes > value )
  }

  // decades
  if(decades.length > 0) {
    filteredFilms = filterDecades(filteredFilms, decades)
  }

  // genres
  if(genres.length > 0) {
    filteredFilms = filterGenres(filteredFilms, genres)
  }

  // ratingImdb
  if(ratings) {
    const {value} = ratings
    filteredFilms = filteredFilms.filter(film => film.imdb.score > value )
  }

  // ratingPlus
  if(ratingsPlus) {
    const {value} = ratingsPlus
    filteredFilms = filteredFilms.filter(film => film.imdb.scoreAlt > value )
  }

  // durations
  if(durations) {
    const {value} = durations
    filteredFilms = filteredFilms.filter(film => film.imdb.runtimeMinutes > value )
  }

  // types
  if(types.length > 0) {
    filteredFilms = filterTypes(filteredFilms, types)
  }

  return filteredFilms
}

function filterSearchTerm(filteredFilms, search) {
  const searchUpper = search.toUpperCase()
  return filteredFilms.filter(film => {
    const title = film.title.toUpperCase()
    return title.includes(searchUpper)
  })
}

function filterDecades(filteredFilms, decades) {
  return filteredFilms.filter(film => {
    const {startYear} = film.imdb
    let isOk = false
    for (const decade of decades) {
      const [min, max] = decade.range
      if(startYear >= min && startYear <= max) {
        isOk = true
        break
      }
    }
    return isOk
  })
}

function filterGenres(filteredFilms, genres) {
  return filteredFilms.filter(film => {
    const genresFilm = film.imdb.genres
    let isOk = false
    for (const genre of genres) {
      if(genresFilm.includes(genre.name)) {
        isOk = true
        break
      }
    }
    return isOk
  })
}

function filterTypes(filteredFilms, types) {
  return filteredFilms.filter(film => {
    const {titleType} = film.imdb
    let isOk = false
    for (const filmType of types) {
      if(titleType === filmType.name) {
        isOk = true
        break
      }
    }
    return isOk
  })
}
