import React, { useState } from 'react';
import '../../css/Grid.css'
import ItemModal from './ItemModal';

function Grid({items, headers, page, setPage, pagesTotal, showFilmName}) {
  const [show, setShow] = useState(false);
  const [modalItem, setModalItem] = useState(items[0])

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  // start https://stackoverflow.com/questions/70612769/how-do-i-recognize-swipe-events-in-react
  const [touchStart, setTouchStart] = useState(null);
  const [touchEnd, setTouchEnd] = useState(null);

  
  const minSwipeDistance = 50; // the required distance between touchStart and touchEnd to be detected as a swipe

  const onTouchStart = (e) => {
    setTouchEnd(null); // otherwise the swipe is fired even with usual touch events
    setTouchStart(e.targetTouches[0].clientX);
  };

  const onTouchMove = (e) => setTouchEnd(e.targetTouches[0].clientX);

  const onTouchEnd = () => {
    if (!touchStart || !touchEnd) return;
    const distance = touchStart - touchEnd;
    const isLeftSwipe = distance > minSwipeDistance;
    const isRightSwipe = distance < -minSwipeDistance;
    if (isRightSwipe && page > 1) setPage(page - 1)
    if (isLeftSwipe && page < pagesTotal) setPage(page + 1)
  }
  // end

  return (
    <div id="grid" className="container" onTouchStart={onTouchStart} onTouchMove={onTouchMove} onTouchEnd={onTouchEnd}>
      <div className="d-flex flex-wrap text-center">
        <ItemModal show={show} modalItem={modalItem} handleClose={handleClose} headers={headers}/>
        {items.map((item, i) => <Item key={i} item={item} headers={headers} handleShow={handleShow} setModalItem={setModalItem} showFilmName={showFilmName}/>)}
      </div>
    </div>
  )
}

function Item({item, headers, handleShow, setModalItem, showFilmName}) {
  return (
    <div className="item m-1 position-relative placeholder">
      <FilmImage item={item} handleShow={handleShow} setModalItem={setModalItem} showFilmName={showFilmName}/>
      <Scores item={item} headers={headers}/>
      <MovieLink item={item} headers={headers}/>
    </div>
  )
}

function FilmImage({item, handleShow, setModalItem, showFilmName}) {
  const handleClick = e => {
    setModalItem(item)
    handleShow()
  }
  let src
  if(!item.image) src = 'https://upload.wikimedia.org/wikipedia/commons/c/c2/No_image_poster.png'
  if(item.image) {
    const replacement = '_V1_QL75_UY281_CR4,0,190,281_'
    src = item.image.replace('_V1_', replacement)
  }

  const titleText = !item.image || showFilmName ? <ImageTitle title={item.title} handleClick={handleClick}/> : ''
  //const titleText = <ImageTitle title={item.title} handleClick={handleClick}/>
  return (
    <div className="position-relative">
      <img className="img-resp" src={src} alt="film poster" onClick={handleClick}/>
      {titleText}
    </div>
  )
}

function ImageTitle({ title, handleClick}) {
  const style = {top: '35%'}
  return (
    <p className='image-text position-absolute w-100 text-wrap text-center' style={style} onClick={handleClick}>
      {title}
    </p>
  )
}

function Scores({item, headers}) {
  const imdbScoreHeader = headers.find( h => h.name === 'rating')
  const altScoreHeader = headers.find( h => h.name === 'rating+')
  // const hasRating = imdbScoreHeader.getVal(item) > 0
  const hasRatingPlus = imdbScoreHeader.getVal(item) > 0
  const scoreHtml = item.imdbId ? <span>{imdbScoreHeader.showVal(item)}</span> : ''
  const altScoreHtml = hasRatingPlus ? <span>{altScoreHeader.showVal(item)}</span> : ''
  return (
    <div className="scores d-flex justify-content-around position-absolute bottom-0 p-1">
      {scoreHtml}
      {altScoreHtml}
    </div>
  )
}

function MovieLink({item, headers}) {
  const channelHeader = headers.find( h => h.name === 'channel')
  const channel = channelHeader.showVal(item)
  return (
    <div className='station d-flex justify-content-end position-absolute top-0 p-1'>
      <span>{channel}</span>
    </div>
  )
}

export default Grid