import getFilmTypes from './infoOptions/getFilmTypes'
import getFilmGenres from './infoOptions/getFilmGenres'
import getFilmDecades from './infoOptions/getFilmDecades'
import getFilmDurations from './infoOptions/getFilmDurations'
import getFilmVotes from './infoOptions/getFilmVotes'
import getFilmRatings from './infoOptions/getFilmRatings'
import getFilmRatingsPlus from './infoOptions/getFilmRatingsPlus'

export default async function fetchData (url, setData, setFilms, setFilmsInfo) {
  const response = await fetch(url)
  if(!response) return setData({ status: 'failure', d: null })
  const dataSet = await response.json()
  if(!dataSet) return setData({ status: 'failure', d: null })

  const { buildDate, films } = dataSet
  
  // set data
  setData({ status: 'success', d: dataSet })
  setFilms(films)

  // set data info
  setFilmsInfo({
    buildDate,
    noOfFilms: films.length,
    options: {
      filmTypes: getFilmTypes(films),
      filmGenres: getFilmGenres(films),
      filmDecades: getFilmDecades(),
      filmDurations: getFilmDurations(),
      filmVotes: getFilmVotes(),
      filmRatings: getFilmRatings(),
      filmRatingsPlus: getFilmRatingsPlus()
    }
  })
}