import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';

function Search({ userFilterPicks, setUserFilterPicks}) {
  const { search } = userFilterPicks
  const [isOpen, setOpen] = useState(false)

  const handleChange = e => {
    e.preventDefault();
    setUserFilterPicks(prevState => {
      const updatedValues = { search : e.target.value}
      return {...prevState, ...updatedValues};
    });
  }

  const handleClick = () => {
    setOpen(!isOpen)
  }

  const SearchBar = isOpen ? (<Form.Control
    type="search"
    placeholder="Search"
    className="me-2"
    aria-label="Search"
    size="sm"
    value={search}
    onChange={handleChange}
  />) : ''
  return (
    <Form className="d-flex">
      <Button variant="outline-secondary" className='m-1' size="sm" onClick={handleClick}><i className="fas fa-search"></i></Button>
      {SearchBar}
    </Form>
  )
}

export default Search