function About() {
  return (
    <div className="container">
      <h1 className="text-center">About This Site</h1>
      <h2>What is Free Films UK?</h2>
      <p><i>Free Films UK</i> is a web app that lists films that can be streamed freely and legally (although you should have a TV license). Where applicable it also provides the IMDb rating for each film on the list to better help the user choose a movie to watch.</p>
      <p>At present, this list is currently limited to films that are available on the following streaming platforms: BBC iPlayer, ITVx, all4 and C5.</p>
      <p>Alongside the IMDb rating an alternative and (I think) more useful metric has also been provided. This is based on the formula used by SteamDb. For more information on this formula click here.</p>
      <h2>Why did you create the site?</h2>
      <p>This site originally began as a way to simply help choose my friday night movie from BBC iPlayer (see here).</p>
      <p>However as inflation and rising energy bills many households in the UK are cancelling their streaming subscriptions. As such this site hopes, in a very small way, to provide an overview of some of the better films that are freely available.</p>
      <h2>When is the site updated?</h2>
      <p>The site is updated twice daily at 10am and 5pm. The time that the data set was published is displayed at the bottom of the films grid</p>
    </div>
  )
}

export default About