export default function getPageItems(page, filmsPerPage, data) {
  const startSlice = (page - 1) * filmsPerPage
  const endSlice = page * filmsPerPage
  const pagesTotal = Math.ceil(data.length/filmsPerPage)
  if( page === pagesTotal) {
    return data.slice(startSlice)
  } else {
    return data.slice(startSlice, endSlice)
  }
}