import React, { useState, useEffect } from 'react'
import './css/App.css'
import fetchData from './funcs/fetchData'
import getUserFilterPicksDefault from './funcs/getUserFilterPicksDefault'
import getUserSettingPicksDefault from './funcs/getUserSettingPicksDefault'
import NavBar from './comps/main/NavBar'
import Films from './comps/main/Films'
import ShortList from './comps/main/ShortList'
import About from './comps/main/About'
import Contact from './comps/main/Contact'
import filterFilms from './funcs/filterFilms'

export default function App({ url }) {
  // STATE
  const [view, setView] = useState('films')
  const [data, setData] = useState({ status: 'fetching', d: false })
  const [films, setFilms] = useState(null)
  const [filmsInfo, setFilmsInfo] = useState(null)
  const [page, setPage] = useState(1)

  const [userSettingPicks, setUserSettingPicks ] = useState(getUserSettingPicksDefault())
  const [userFilterPicks, setUserFilterPicks] = useState(getUserFilterPicksDefault())

  const [windowSize, setWindowSize] = useState(getWindowSize())

  // EFFECTS
  // get data
  useEffect(() => {
    fetchData(url, setData, setFilms, setFilmsInfo)
  }, [url])

  // filter data
  useEffect(() => {
    if(data.d) {
      const filteredFilms = filterFilms(data.d.films, userFilterPicks)
      setFilms(filteredFilms)
    }
  }, [data.d, userFilterPicks])

  useEffect(() => {
    setPage(1)
  }, [userFilterPicks])

  // user screen dimensions
  useEffect(() => {
    const handleWindowResize = () => setWindowSize(getWindowSize())
    window.addEventListener('resize', handleWindowResize); // the effect
    return () => window.removeEventListener('resize', handleWindowResize); // Specify how to clean up after this effect
  }, []);


  // VIEW
  let content
  if(view === 'films')  content = (
    <Films 
      status={data.status}
      films={films}
      filmsInfo={filmsInfo}
      userSettingPicks={userSettingPicks}
      setUserSettingPicks={setUserSettingPicks}
      userFilterPicks={userFilterPicks}
      setUserFilterPicks={setUserFilterPicks}
      page={page}
      setPage={setPage}
      windowSize={windowSize}
    />)
  if(view === 'shortlist')  content = <ShortList/>
  if(view === 'about')  content = <About/>
  if(view === 'contact')  content = <Contact/>

  // RENDER
  return (
    <main id="app" className="">
      <NavBar view={view} setView={setView}/>
      { content }
    </main>
  )
}

function getWindowSize() {
  const {innerWidth, innerHeight} = window;
  return {innerWidth, innerHeight};
}