import Loader from "../misc/Loader";
import Failure from "../misc/Failure";
import FilmHeaders from '../secondary/FilmsHeader'
import getHeaders from '../../funcs/getHeaders'
import Grid from '../secondary/Grid'
import Paginator from "../secondary/Paginator";
import orderFilms from "../../funcs/orderFilms";
import getPageItems from "../../funcs/getPageItems";

export default function Films({ status, films, filmsInfo, userSettingPicks, setUserSettingPicks, userFilterPicks, setUserFilterPicks, page, setPage, windowSize}) {
  let content;
  if(status === 'fetching') content = <Loader/>
  if(status === 'failure') content = <Failure/>
  if(status === 'success') content = (
    <FilmsSuccess
      films={films}
      filmsInfo={filmsInfo}
      userSettingPicks={userSettingPicks}
      setUserSettingPicks={setUserSettingPicks}
      userFilterPicks={userFilterPicks}
      setUserFilterPicks={setUserFilterPicks}
      page={page}
      setPage={setPage}
      windowSize={windowSize}
    />)
  return <>{content}</>
  
}

function FilmsSuccess({ films, filmsInfo, userSettingPicks, setUserSettingPicks, userFilterPicks, setUserFilterPicks, page, setPage, windowSize}) {
  const filmsPerPage = getFilmsPerPage(windowSize.innerHeight, windowSize.innerWidth)
  const headers = getHeaders()
  const orderedFilms = orderFilms(films, headers, userSettingPicks.order)
  const items = getPageItems(page, filmsPerPage, orderedFilms)
  const pagesTotal = Math.ceil(films.length/filmsPerPage)
  const { showFilmName } = userSettingPicks
  return (
    <div className="bg-cover">
      <FilmHeaders headers={headers} userSettingPicks={userSettingPicks} setUserSettingPicks={setUserSettingPicks} userFilterPicks={userFilterPicks}
      setUserFilterPicks={setUserFilterPicks} filmsInfo={filmsInfo}/>
      <p className="text-center"><span className="badge bg-secondary">{films.length} Films</span></p>
      <Grid items={items} headers={headers} page={page} setPage={setPage} pagesTotal={pagesTotal} showFilmName={showFilmName}/>
      <Paginator page={page} setPage={setPage} filmsPerPage={filmsPerPage} total={films.length}/>
    </div>
  )
}

function getFilmsPerPage(innerHeight, innerWidth) {
  if(innerWidth > 992) return 18
  if(innerHeight > 800) return 15
  return 12
}