import Modal from 'react-bootstrap/Modal';
import Form from 'react-bootstrap/Form';

function Settings({ showSettings, handleClose, userSettingPicks, setUserSettingPicks }) {

  return (
    <>
      <Modal
        show={showSettings}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title>Settings</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <SwitchExample userSettingPicks={userSettingPicks} setUserSettingPicks={setUserSettingPicks}/>
        </Modal.Body>
        <Modal.Footer>
        </Modal.Footer>
      </Modal>
    </>
  );
}

function SwitchExample({userSettingPicks, setUserSettingPicks}) {
  const { showFilmName } = userSettingPicks
  const handleClick = e => {

    setUserSettingPicks(prevState => {
      const updatedValues = { showFilmName : !showFilmName}
      return {...prevState, ...updatedValues};
    });
  }
  return (
    <Form>
      <Form.Check 
        type="switch"
        id="custom-switch"
        label="Show Film Names"
        onClick={handleClick}
      />
    </Form>
  );
}

export default Settings